import React from "react";
import { Col } from "react-bootstrap";
import Rating from "./Rating";

const BookItem = ({ book, category }) => (
  <Col xs={6} sm={6} md={4} lg={3} className="animateTest">
    <div className="cover">
      <img
        className="img-fluid"
        src="https://about.canva.com/wp-content/uploads/sites/3/2015/01/children_bookcover.png"
        alt={book.title}
      />
      <div className="content">
        <h2>{book.title}</h2>
        <h5> by {book.author}</h5>
        <p>{book.description}</p>
        <span className={`${category} badge-category`}>{category}</span>
        <br />
        <Rating value={book.rating} />
      </div>
    </div>
  </Col>
);

export default BookItem;
