import React from "react";

const Rating = ({ value }) => {
  const total = 5;
  const starpercenetage = (value * 100) / total;
  //   console.log(starpercenetage);
  const rating = `${Math.round(starpercenetage / 10) * 10}%`;
  //   console.log();
  return (
    <div className="rating-all">
      <div className="rating-inner" style={{ width: rating }} />
    </div>
  );
};

export default Rating;
