import React from "react";
import { connect } from "react-redux";
import { bookTypes } from "../../Books";
import { filterBooks } from "../operations";
import SearchTitleInput from "./SearchTitleInput";
const NavFilter = ({ filterBooks, filterId, category }) => (
  <div class="filters d-flex flex-column  flex-lg-row ">
    <ul class="nav align-items-center justify-content-center">
      {category.map(cat => (
        <li
          key={cat.id}
          className={`nav-item px-3 text-sm-center  ${
            filterId === cat.id ? ` nav-active ${cat.name} ` : null
            }`}
          onClick={() => filterBooks(cat.id)}
        >
          {cat.name}
        </li>
      ))}
    </ul>
    <SearchTitleInput />

  </div>
);

const mapStateToProps = state => ({
  filterId: state.books.filterId,
  category: state.category.list
});

const mapDispatchToProps = dispatch => ({
  filterBooks: id => dispatch(filterBooks(id))
});
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(NavFilter);
