import React, { useState } from "react";
import { connect } from "react-redux";
import "./book.css";
import * as R from "ramda";
import { getAllBooks, deleteOneBook, loadMoreBook } from "../operations";
import BookItem from "./BookItem";
const BooksContainer = ({ books, getAllBooks, deleteOneBook, categorys, loadMoreBook }) => {
  // useEffect(() => {
  //   getAllBooks();
  // }, [getAllBooks]);
  const [LoadFlag, setLoadFlag] = useState(true);

  const setCatagoryFromId = id => {
    const category = categorys.filter(cat => cat.id === id);
    // console.log(category[0].name);
    return category[0].name;
  };

  let currentBooks = books.filterId
    ? books.list.filter(book => book.category === books.filterId)
    : books.list;

  currentBooks = books.filterSearch
    ? currentBooks.filter(book =>
      R.includes(books.filterSearch.toLowerCase(), book.title.toLowerCase())
    )
    : currentBooks;


  currentBooks = currentBooks.slice(0, books.visibility);
  const isMoreBooks = currentBooks.length < books.visibility;
  console.log(isMoreBooks)

  return (
    <>
      <div className=" row p-2">
        {currentBooks.map(book => (
          <BookItem
            key={book.id}
            book={book}
            category={setCatagoryFromId(book.category)}
          />
        ))}


      </div>

      {isMoreBooks ? null : <div className="row">
        <button onClick={loadMoreBook} className="btn btn btn-primary m-auto">Load More</button>
      </div>}

    </>
  );
};

const mapStateToProps = state => ({
  books: state.books,
  categorys: state.category.list,
});

const mapDispatchToProps = dispatch => ({
  getAllBooks: () => dispatch(getAllBooks()),
  deleteOneBook: id => dispatch(deleteOneBook(id)),
  loadMoreBook: () => dispatch(loadMoreBook())
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(BooksContainer);
