import actionsBook from "./actions";
// import API from "../../API/router";
const fetchBooks = async () => {
  const response = await fetch("http://localhost:8000/books/", {
    method: "GET"
  });
  console.log(response);
  const json = await response.json();

  return json;
};

const deleteBook = async id => {
  const response = await fetch(`http://localhost:8000/books/${id}`, {
    method: "DELETE"
  });

  return response;
};

export const getAllBooks = () => async dispatch => {
  const books = await fetchBooks();
  console.log(books);
  books.map(book => dispatch(actionsBook.add(book)));
};

export const deleteOneBook = id => async dispatch => {
  const deleted = await deleteBook(id);
  dispatch(actionsBook.del(id));
};

export const filterBooks = category_id => dispatch => {
  console.log(category_id);
  dispatch(actionsBook.filter(category_id));
};

export const changeValueSearch = value => dispatch => {
  dispatch(actionsBook.search(value));
};

export const loadMoreBook = ()=> dispatch =>{
  dispatch(actionsBook.loadMore());
}
