import types from "./types.js";

const add = item => ({
  type: types.ADD_BOOK,
  item
});

const reset = item => ({
  type: types.RESET_BOOK,
  item
});

const del = id =>({
  type:types.DELETE_BOOK,
  id
})

const filter=category_id=>({
  type:types.FILTER_BOOK,
  category_id
})
const search=valueInputSearchTitle=>({
  type:types.SEARCH_TITLE_BOOK,
  valueInputSearchTitle
})

const loadMore=()=>({
  type:types.LOAD_MORE_BOOK
})
export default {
  add,
  reset,
  del,
  filter,
  search,
  loadMore,
};
