const bookReducer = (state = INITIAL_STATE, action) => {
	switch (action.type) {
		case types.ADD_BOOK:
			return produce(state, draftState => {
				draftState.list.push(action.item);
			});

		case types.RESET_BOOK:
			return produce(state, draftState => {
				draftState.list = [];
			});
		case types.DELETE_BOOK:
			return produce(state, draftState => {
				const index = draftState.list.findIndex(book => book.id === action.id);
				draftState.list.splice(index, 1);
			});
		case types.FILTER_BOOK:
			return produce(state, draftState => {
				draftState.filterId = action.category_id;
			});

		case types.SEARCH_TITLE_BOOK:
			return produce(state, draftState => {
				const isNotNull = action.valueInputSearchTitle === '' ? null : action.valueInputSearchTitle;
				draftState.filterSearch = isNotNull;
			});
		case types.LOAD_MORE_BOOK:
			return produce(state, draftState => {
				const defaultLoad = 4;
				draftState.visibility = draftState.visibility + defaultLoad;
			});
		default:
			return state;
	}
};
export default bookReducer;
