import React from "react";
import { connect } from "react-redux";
import { changeValueSearch } from "../operations";

const SearchTitleInput = ({ changeValueSearch }) => {
  return (
    <div className=" p-3 ml-lg-auto">
      <div className="input-group flex-1">
        <input
          type="text"
          name="searchInput"
          onChange={e => changeValueSearch(e.target.value)}
          className="form-control"
          placeholder="search Title..."
        />
        <div className="input-group-append">
          <span className="input-group-text" id="basic-addon2">
            <i className="fa fa-search" aria-hidden="true" />
          </span>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = state => ({
  searchInputValue: state.books.searchInput
});

const mapDispatchToProps = dispatch => ({
  changeValueSearch: value => dispatch(changeValueSearch(value))
});
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(SearchTitleInput);
